body {
    margin: 0;
    scroll-behavior: smooth;
    height: 0;
    height: 100%;
}
.about-container {
    max-width: 1330px;
    margin: 0 auto;
    padding: 40px 20px;
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    color: #333;
}

.about-title {
    font-size: 2.5em;
    text-align: center;
    color:  white;
    position: relative;
    top: -340px;
}

.about-intro {
    font-size: 1.2em;
    margin-bottom: 30px;
    text-align: center;
    position: relative;
    top: -350px;
    color: white;
}

.about-top-content {
    background-color: #5478d2;
    width: 100%;
    right: 1px;
    position: relative;
    height: 390px;
}

.section-title {
    font-size: 2em;
    bottom: 200px;
    position: relative;
    color:  #5478d2;
    margin-left: 450px;
    display: inline-block; 
    border-bottom: 1px solid black;
    padding-bottom: 5px;
}

.section-title2 {
    font-size: 2em;
    position: relative;
    bottom: 200px;
    color:  #5478d2;
    margin-left: 580px;
    display: inline-block; 
    border-bottom: 1px solid black;
    padding-bottom: 5px;
}

.section-title3 {
    font-size: 2em;
    position: relative;
    bottom: 210px;
    color:  #5478d2;
    margin-left: 530px;
    display: inline-block; 
    border-bottom: 1px solid black;
    padding-bottom: 5px;
    margin-top: 130px;
}

.section-title4 {
    font-size: 2em;
    position: relative;
    bottom: 210px;
    color:  #5478d2;
    margin-left: 530px;
    display: inline-block; 
    border-bottom: 1px solid black;
    padding-bottom: 5px;
    margin-top: 120px;
}

.section-title5 {
    font-size: 2em;
    position: relative;
    bottom: 210px;
    color:  #5478d2;
    margin-left: 490px;
    display: inline-block; 
    border-bottom: 1px solid black;
    padding-bottom: 5px;
    margin-top: 120px;
}


.section-content {
    font-size: 1.2em;
    bottom: 200px;
    text-align: center;
    position: relative;
}

.product-category {
    margin-top: 20px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    width: 40%;
    height: 380px;
    margin-left: 150px;
}

.product-category1 {
    position: relative;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    width: 40%;
    height: 380px;
}

.product-category2 {
    margin-top: 60px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    width: 40%;
    height: 380px;
    margin-left: 10px;
}


.FaceWash {
    width: 25%;
    object-fit: cover;
    margin-left: 30px;
    transition: transform 0.2s ease-in-out;
}

.FaceWash:hover {
    transform: scale(1.05); 
}

.FaceWash1 {
    width: 15%;
    margin-left: 120px;
    transition: transform 0.2s ease-in-out;
    object-fit: cover;
}

.FaceWash1:hover {
    transform: scale(1.05);
}

.section1 {
    display: flex;
    top: -190px;
    position: relative;
}

.section2 {
    display: flex;
    top: -180px;
    position: relative;
    margin-left: 90px;
}

.section3 {
    display: flex;
    top: -190px;
    position: relative;
}

.product-title {
    font-size: 1.5em;
    margin-bottom: 10px;
    color: #5478d2;
    display: flex;
}

.product-description {
    text-align: justify;
    font-weight: 300;
}

.why-choose-us-list {
    list-style: none;
    padding: 0;
    display: flex;
    position: relative;
    top:-190px;
}

.why-choose-us-list1 {
    list-style: none;
    padding: 0;
    display: flex;
    margin-top: 60px;
    height: 30%;
    justify-content: space-between;
    position: relative;
    top:-190px;
}

.why-choose-us-item {
    margin-bottom: 20px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
}

.why-choose-us-item:hover {
    transform: scale(1.05); 
    background-color: #5478d2;
    color: white;
}

.why-choose-us-item2 {
    margin-bottom: 20px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    margin-left: 30px;
    transition: transform 0.2s ease-in-out;
}

.why-choose-us-item2:hover {
    transform: scale(1.05); 
    background-color: #5478d2;
    color: white;
}

.why-choose-us-item3 {
    margin-bottom: 20px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    margin-left: 20px;
    transition: transform 0.2s ease-in-out;
}

.why-choose-us-item3:hover {
    transform: scale(1.05); 
    background-color: #5478d2;
    color: white;
}



.why-choose-us-item h4 {
    margin-bottom: 10px;
}

.testimonials {
    display: flex;
    position: relative;
    bottom: 150px;
}

.testimonial {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    text-align: center;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
    margin-left: 125px;
    font-size: 21px;
}

.testimonial:hover {
    transform: scale(1.05); 
    background-color: #5478d2;
    color: white;
}

.rating {
    color: #f39c12;
    font-size: 1.2em;
    top: -10px;
    display: flex;
    position: relative;
    justify-content: center;
}

@media screen and (max-width: 1280px) {
  .section-title {
    right: 45px;
  }

  .section-title2 {
    right: 75px;
  }

  .section-title3 {
    right: 65px;
  }

  .section-title4 {
    right: 45px;
  }

  .section-title5 {
    right: 45px;
  }

  .testimonials{
        position: relative;
        margin-left: 50px;
        width: 990px;
    }

    .testimonial {
        font-size: 24px;
    }

    .why-choose-us-item {
        font-size: 19px;
    }

    .why-choose-us-item2 {
        font-size: 19px;
    }

    .why-choose-us-item3 {
        font-size: 19px;
    }

    h4 {
        font-size: 24px;
    }

    p {
        font-size: 18px;
    }

    .FaceWash {
        width: 30%;
        object-fit: cover;
        margin-top: 90px;
    }

    .FaceWash1{
        margin-top: 90px;
    }

    .product-title {
